interface Listing {
  id: string;
  item_name: string;
  photo_url?: string;
  apartment_number?: string;
  building_number?: string;
  days_to_expiry?: number;
  status: 'available' | 'claimed' | 'completed';
  building_id: string;
  user_id: string;
  owner_name?: string;
  created_at: string;
}

interface CreateListingRequest {
  item_name: string;
  photo_url?: string;
  apartment_number?: string;
  building_number?: string;
  days_to_expiry?: number;
}

class ApiService {
  private static instance: ApiService;
  private baseUrl: string;
  private authToken: string | null = null;

  constructor() {
    // Use environment variable or default to localhost
    this.baseUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000';
  }

  static getInstance(): ApiService {
    if (!ApiService.instance) {
      ApiService.instance = new ApiService();
    }
    return ApiService.instance;
  }

  // Set authentication token
  setAuthToken(token: string): void {
    this.authToken = token;
  }

  // Get authorization headers
  private getAuthHeaders(): HeadersInit {
    const headers: HeadersInit = {
      'Content-Type': 'application/json',
    };
    
    if (this.authToken) {
      headers['Authorization'] = `Bearer ${this.authToken}`;
    }
    
    return headers;
  }

  // Get all available listings
  async getListings(): Promise<Listing[]> {
    try {
      const response = await fetch(`${this.baseUrl}/listings`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error(`Failed to fetch listings: ${response.statusText}`);
      }

      const listings = await response.json();
      return listings;
    } catch (error) {
      console.warn('Backend not available, using mock data:', error);
      // Return mock data when backend is not available
      return this.getMockListings();
    }
  }

  // Mock data for when backend is not available
  private getMockListings(): Listing[] {
    return [
      {
        id: 'mock-1',
        item_name: 'Pizza Slices',
        photo_url: undefined,
        apartment_number: '101',
        building_number: 'Edge',
        days_to_expiry: 2,
        status: 'available',
        building_id: 'edge',
        user_id: 'mock-user-1',
        owner_name: 'Alice',
        created_at: new Date(Date.now() - 2 * 60 * 60 * 1000).toISOString() // 2 hours ago
      },
      {
        id: 'mock-2',
        item_name: 'Energy Drinks',
        photo_url: undefined,
        apartment_number: '201',
        building_number: 'TechTerrace',
        days_to_expiry: 5,
        status: 'available',
        building_id: 'techterrace',
        user_id: 'mock-user-2',
        owner_name: 'Bob',
        created_at: new Date(Date.now() - 1 * 60 * 60 * 1000).toISOString() // 1 hour ago
      },
      {
        id: 'mock-3',
        item_name: 'Leftover Pasta',
        photo_url: undefined,
        apartment_number: '102',
        building_number: 'Edge',
        days_to_expiry: 1,
        status: 'claimed',
        building_id: 'edge',
        user_id: 'mock-user-3',
        owner_name: 'Charlie',
        created_at: new Date(Date.now() - 30 * 60 * 1000).toISOString() // 30 minutes ago
      }
    ];
  }

  // Get listings filtered by building
  async getListingsByBuilding(buildingId: string): Promise<Listing[]> {
    try {
      const allListings = await this.getListings();
      return allListings.filter(listing => listing.building_id === buildingId);
    } catch (error) {
      console.error('Error fetching listings by building:', error);
      throw error;
    }
  }

  // Create a new listing
  async createListing(listingData: CreateListingRequest): Promise<Listing> {
    try {
      const response = await fetch(`${this.baseUrl}/listings`, {
        method: 'POST',
        headers: this.getAuthHeaders(),
        body: JSON.stringify(listingData),
      });

      if (!response.ok) {
        throw new Error(`Failed to create listing: ${response.statusText}`);
      }

      const listing = await response.json();
      return listing;
    } catch (error) {
      console.error('Error creating listing:', error);
      throw error;
    }
  }

  // Upload photo and create listing
  async createListingWithPhoto(photoFile: File, apartmentNumber?: string, buildingNumber?: string): Promise<Listing> {
    try {
      const formData = new FormData();
      formData.append('photo', photoFile);
      
      if (apartmentNumber) {
        formData.append('apartment_number', apartmentNumber);
      }
      if (buildingNumber) {
        formData.append('building_number', buildingNumber);
      }

      const headers: HeadersInit = {};
      if (this.authToken) {
        headers['Authorization'] = `Bearer ${this.authToken}`;
      }

      const response = await fetch(`${this.baseUrl}/listings/photo`, {
        method: 'POST',
        headers,
        body: formData,
      });

      if (!response.ok) {
        throw new Error(`Failed to create listing with photo: ${response.statusText}`);
      }

      const listing = await response.json();
      return listing;
    } catch (error) {
      console.error('Error creating listing with photo:', error);
      throw error;
    }
  }

  // Create listing with sample photo
  async createSampleListing(apartmentNumber?: string, buildingNumber?: string): Promise<any> {
    try {
      const requestBody: any = {};
      
      if (apartmentNumber) {
        requestBody.apartment_number = apartmentNumber;
      }
      if (buildingNumber) {
        requestBody.building_number = buildingNumber;
      }

      const response = await fetch(`${this.baseUrl}/listings/photo/sample`, {
        method: 'POST',
        headers: this.getAuthHeaders(),
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        throw new Error(`Failed to create sample listing: ${response.statusText}`);
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.warn('Backend not available - simulating listing creation:', error);
      // Return mock result when backend is not available
      return {
        listing: {
          id: `mock-new-${Date.now()}`,
          item_name: 'Sample Food Item',
          photo_url: undefined,
          apartment_number: apartmentNumber,
          building_number: buildingNumber,
          status: 'available',
          building_id: buildingNumber || 'edge',
          user_id: 'mock-user',
          owner_name: 'You',
          created_at: new Date().toISOString()
        },
        item_name: 'Sample Food Item',
        photoUrl: undefined
      };
    }
  }

  // Claim a listing
  async claimListing(listingId: string): Promise<void> {
    try {
      const response = await fetch(`${this.baseUrl}/listings/${listingId}/claim`, {
        method: 'PUT',
        headers: this.getAuthHeaders(),
      });

      if (!response.ok) {
        throw new Error(`Failed to claim listing: ${response.statusText}`);
      }
      console.log(`‚úÖ Successfully claimed listing ${listingId}`);
    } catch (error) {
      console.warn('Backend not available - simulating claim:', error);
      // Simulate success when backend is not available
      console.log(`‚úÖ Simulated claim of listing ${listingId}`);
    }
  }

  // Complete a listing (award points)
  async completeListing(listingId: string): Promise<void> {
    try {
      const response = await fetch(`${this.baseUrl}/listings/${listingId}/complete`, {
        method: 'PUT',
        headers: this.getAuthHeaders(),
      });

      if (!response.ok) {
        throw new Error(`Failed to complete listing: ${response.statusText}`);
      }
      console.log(`üèÜ Successfully completed listing ${listingId}`);
    } catch (error) {
      console.warn('Backend not available - simulating completion:', error);
      // Simulate success when backend is not available
      console.log(`üèÜ Simulated completion of listing ${listingId}`);
    }
  }

  // Mock authentication for demo purposes
  async mockLogin(buildingId: string = 'edge'): Promise<string> {
    // In a real app, this would make an actual API call to authenticate
    // For now, we'll create a mock token
    const mockToken = `mock-token-${buildingId}-${Date.now()}`;
    this.setAuthToken(mockToken);
    return mockToken;
  }
}

export default ApiService;
export type { Listing, CreateListingRequest };
