import ApiService, { Listing } from './ApiService';

export interface FridgeItem {
  id: string;
  name: string;
  quantity: number;
  addedBy: string;
  dateAdded: Date;
  expirationDate?: Date;
  category: 'dairy' | 'meat' | 'vegetables' | 'fruits' | 'leftovers' | 'beverages' | 'condiments' | 'other';
  // Database listing properties
  listingId?: string;
  photoUrl?: string;
  status?: 'available' | 'claimed' | 'completed';
  ownerName?: string;
  apartmentNumber?: string;
  buildingNumber?: string;
}

export interface FridgeData {
  items: FridgeItem[];
  lastUpdated: Date;
  apartmentId: string;
  residents: string[];
}

class FridgeManager {
  private static instance: FridgeManager;
  private fridgeData: Map<string, FridgeData> = new Map();
  private apiService: ApiService;
  private isLoading: boolean = false;
  private lastRefresh: Map<string, number> = new Map();
  private refreshInterval: number = 30000; // 30 seconds
  private uiState: {
    isOpen: boolean;
    currentFridge: string | null;
    selectedItemIndex: number;
  } = {
    isOpen: false,
    currentFridge: null,
    selectedItemIndex: 0
  };

  static getInstance(): FridgeManager {
    if (!FridgeManager.instance) {
      FridgeManager.instance = new FridgeManager();
    }
    return FridgeManager.instance;
  }

  constructor() {
    this.apiService = ApiService.getInstance();
    // Mock login for demo - in real app this would be handled by auth system
    this.apiService.mockLogin('edge');
  }


  // Database integration methods
  private async loadListingsFromDatabase(fridgeId: string): Promise<FridgeItem[]> {
    try {
      this.isLoading = true;
      const buildingId = fridgeId; // Map fridge ID to building ID
      const listings = await this.apiService.getListingsByBuilding(buildingId);
      
      console.log(`üì¶ Loaded ${listings.length} listings for ${fridgeId}:`, listings);
      
      const fridgeItems: FridgeItem[] = listings.map((listing: Listing) => ({
        id: `listing-${listing.id}`,
        listingId: listing.id,
        name: listing.item_name,
        quantity: 1, // Listings typically represent single items
        addedBy: listing.owner_name || 'Unknown',
        dateAdded: new Date(listing.created_at),
        expirationDate: listing.days_to_expiry 
          ? new Date(Date.now() + listing.days_to_expiry * 24 * 60 * 60 * 1000)
          : undefined,
        category: this.categorizeItem(listing.item_name),
        photoUrl: listing.photo_url,
        status: listing.status,
        ownerName: listing.owner_name,
        apartmentNumber: listing.apartment_number,
        buildingNumber: listing.building_number
      }));
      
      return fridgeItems;
    } catch (error) {
      console.error('Failed to load listings from database:', error);
      return []; // Return empty array on error, keep fallback items
    } finally {
      this.isLoading = false;
    }
  }

  private categorizeItem(itemName: string): FridgeItem['category'] {
    const name = itemName.toLowerCase();
    
    if (name.includes('milk') || name.includes('cheese') || name.includes('yogurt') || name.includes('butter')) {
      return 'dairy';
    }
    if (name.includes('meat') || name.includes('chicken') || name.includes('beef') || name.includes('pork')) {
      return 'meat';
    }
    if (name.includes('lettuce') || name.includes('carrot') || name.includes('vegetable') || name.includes('broccoli')) {
      return 'vegetables';
    }
    if (name.includes('apple') || name.includes('banana') || name.includes('orange') || name.includes('fruit')) {
      return 'fruits';
    }
    if (name.includes('pizza') || name.includes('leftover') || name.includes('sandwich') || name.includes('pasta')) {
      return 'leftovers';
    }
    if (name.includes('soda') || name.includes('juice') || name.includes('drink') || name.includes('water') || name.includes('energy')) {
      return 'beverages';
    }
    if (name.includes('sauce') || name.includes('ketchup') || name.includes('mustard') || name.includes('mayo')) {
      return 'condiments';
    }
    
    return 'other';
  }

  private async refreshFridgeData(fridgeId: string): Promise<void> {
    const lastRefresh = this.lastRefresh.get(fridgeId) || 0;
    const now = Date.now();
    
    // Only refresh if enough time has passed
    if (now - lastRefresh > this.refreshInterval) {
      console.log(`üîÑ Refreshing data for fridge ${fridgeId}`);
      
      const databaseItems = await this.loadListingsFromDatabase(fridgeId);
      const fridge = this.fridgeData.get(fridgeId);
      
      if (fridge) {
        // Keep manually added items (those without listingId) and merge with database items
        const manualItems = fridge.items.filter(item => !item.listingId);
        fridge.items = [...manualItems, ...databaseItems];
        fridge.lastUpdated = new Date();
      }
      
      this.lastRefresh.set(fridgeId, now);
    }
  }

  // Public methods - everyone can view
  public async getFridgeItems(fridgeId: string): Promise<FridgeItem[]> {
    // Refresh data from database if needed
    await this.refreshFridgeData(fridgeId);
    
    const fridge = this.fridgeData.get(fridgeId);
    return fridge ? [...fridge.items] : [];
  }

  public canPlayerModify(fridgeId: string, playerId: string): boolean {
    const fridge = this.fridgeData.get(fridgeId);
    return fridge ? fridge.residents.includes(playerId) : false;
  }

  // UI State Management
  public async openFridgeUI(fridgeId: string): Promise<void> {
    this.uiState.isOpen = true;
    this.uiState.currentFridge = fridgeId;
    this.uiState.selectedItemIndex = 0;
    
    // Force refresh when opening fridge
    this.lastRefresh.delete(fridgeId);
    await this.refreshFridgeData(fridgeId);
  }

  public closeFridgeUI(): void {
    this.uiState.isOpen = false;
    this.uiState.currentFridge = null;
    this.uiState.selectedItemIndex = 0;
  }

  public isFridgeUIOpen(): boolean {
    return this.uiState.isOpen;
  }

  public getCurrentFridge(): string | null {
    return this.uiState.currentFridge;
  }

  // Modification methods (only for residents)
  public addItem(fridgeId: string, item: Omit<FridgeItem, 'id' | 'dateAdded'>, playerId: string): boolean {
    if (!this.canPlayerModify(fridgeId, playerId)) {
      console.log(`Player ${playerId} cannot modify fridge ${fridgeId} - not a resident`);
      return false;
    }

    const fridge = this.fridgeData.get(fridgeId);
    if (!fridge) return false;

    const newItem: FridgeItem = {
      ...item,
      id: `${item.category}-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      dateAdded: new Date(),
      addedBy: playerId
    };

    fridge.items.push(newItem);
    fridge.lastUpdated = new Date();
    
    console.log(`Added item: ${newItem.name} to fridge ${fridgeId} by ${playerId}`);
    return true;
  }

  public removeItem(fridgeId: string, itemId: string, playerId: string): boolean {
    if (!this.canPlayerModify(fridgeId, playerId)) {
      console.log(`Player ${playerId} cannot modify fridge ${fridgeId} - not a resident`);
      return false;
    }

    const fridge = this.fridgeData.get(fridgeId);
    if (!fridge) return false;

    const itemIndex = fridge.items.findIndex(item => item.id === itemId);
    if (itemIndex === -1) {
      console.log(`Item ${itemId} not found in fridge ${fridgeId}`);
      return false;
    }

    const removedItem = fridge.items.splice(itemIndex, 1)[0];
    fridge.lastUpdated = new Date();
    
    console.log(`Removed item: ${removedItem.name} from fridge ${fridgeId} by ${playerId}`);
    return true;
  }

  // UI Rendering
  public async renderFridgeUI(ctx: CanvasRenderingContext2D, playerId: string): Promise<void> {
    if (!this.uiState.isOpen || !this.uiState.currentFridge) return;

    const fridgeId = this.uiState.currentFridge;
    const items = await this.getFridgeItems(fridgeId);
    const canModify = this.canPlayerModify(fridgeId, playerId);

    const uiWidth = 400;
    const uiHeight = 500;
    const uiX = (ctx.canvas.width - uiWidth) / 2;
    const uiY = (ctx.canvas.height - uiHeight) / 2;

    ctx.save();
    
    // Background overlay
    ctx.fillStyle = 'rgba(0, 0, 0, 0.8)';
    ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    
    // Fridge UI panel
    ctx.fillStyle = '#F8F9FA';
    ctx.fillRect(uiX, uiY, uiWidth, uiHeight);
    
    // Header
    ctx.fillStyle = '#2C3E50';
    ctx.fillRect(uiX, uiY, uiWidth, 40);
    
    ctx.fillStyle = '#FFFFFF';
    ctx.font = 'bold 16px Arial';
    ctx.textAlign = 'center';
    const apartmentName = fridgeId === 'edge' ? 'Edge Apartment' : 'Tech Terrace';
    ctx.fillText(`üßä ${apartmentName} Fridge`, uiX + uiWidth / 2, uiY + 25);
    
    // Close button
    ctx.fillStyle = '#E74C3C';
    ctx.fillRect(uiX + uiWidth - 35, uiY + 5, 30, 30);
    ctx.fillStyle = '#FFFFFF';
    ctx.font = 'bold 16px Arial';
    ctx.fillText('√ó', uiX + uiWidth - 20, uiY + 25);
    
    // Permission indicator
    ctx.fillStyle = canModify ? '#27AE60' : '#F39C12';
    ctx.font = '12px Arial';
    ctx.textAlign = 'left';
    const permissionText = canModify ? '‚úì Resident (can modify)' : 'üëÅ Visitor (view only)';
    ctx.fillText(permissionText, uiX + 10, uiY + 60);
    
    // Items list
    this.renderItemsList(ctx, items, uiX + 10, uiY + 80, uiWidth - 20, uiHeight - 120);
    
    // Loading indicator
    if (this.isLoading) {
      ctx.fillStyle = '#3498DB';
      ctx.font = '12px Arial';
      ctx.textAlign = 'center';
      ctx.fillText('Loading...', uiX + uiWidth / 2, uiY + 420);
    }

    // Instructions
    ctx.fillStyle = '#7F8C8D';
    ctx.font = '10px Arial';
    ctx.textAlign = 'center';
    if (canModify) {
      ctx.fillText('A: Add local item | P: Add listing | C: Claim | X: Complete | D: Delete | ‚Üë‚Üì: Navigate | ESC: Close', uiX + uiWidth / 2, uiY + uiHeight - 20);
    } else {
      ctx.fillText('C: Claim | X: Complete | ‚Üë‚Üì: Navigate | ESC: Close', uiX + uiWidth / 2, uiY + uiHeight - 20);
    }
    
    ctx.restore();
  }

  private renderItemsList(ctx: CanvasRenderingContext2D, items: FridgeItem[], x: number, y: number, width: number, height: number): void {
    if (items.length === 0) {
      ctx.fillStyle = '#95A5A6';
      ctx.font = '14px Arial';
      ctx.textAlign = 'center';
      ctx.fillText('Fridge is empty! üèÉ‚Äç‚ôÇÔ∏èüí®', x + width / 2, y + height / 2);
      return;
    }
    
    const itemHeight = 40;
    const maxVisibleItems = Math.floor(height / itemHeight);
    
    // Group items by category
    const categories: { [key: string]: FridgeItem[] } = {};
    items.forEach(item => {
      if (!categories[item.category]) {
        categories[item.category] = [];
      }
      categories[item.category].push(item);
    });
    
    let currentY = y;
    let itemIndex = 0;
    
    Object.entries(categories).forEach(([category, categoryItems]) => {
      // Category header
      ctx.fillStyle = '#34495E';
      ctx.font = 'bold 12px Arial';
      ctx.textAlign = 'left';
      ctx.fillText(`üìÅ ${category.toUpperCase()}`, x, currentY);
      currentY += 20;
      
      // Items in category
      categoryItems.forEach((item) => {
        const isSelected = itemIndex === this.uiState.selectedItemIndex;
        this.renderFridgeItem(ctx, item, x + 10, currentY, width - 20, itemHeight - 5, isSelected);
        currentY += itemHeight;
        itemIndex++;
      });
      
      currentY += 10; // Space between categories
    });
  }

  private renderFridgeItem(ctx: CanvasRenderingContext2D, item: FridgeItem, x: number, y: number, width: number, height: number, isSelected: boolean): void {
    const isExpiringSoon = item.expirationDate && 
      (item.expirationDate.getTime() - Date.now()) < (2 * 24 * 60 * 60 * 1000);
    const isListing = !!item.listingId;
    
    // Item background - different colors for different states
    if (isSelected) {
      ctx.fillStyle = '#3498DB'; // Blue selection
    } else if (item.status === 'claimed') {
      ctx.fillStyle = '#D5DBDB'; // Gray for claimed
    } else if (item.status === 'completed') {
      ctx.fillStyle = '#D5F4E6'; // Light green for completed
    } else if (isExpiringSoon) {
      ctx.fillStyle = '#FADBD8'; // Red for expiring
    } else if (isListing) {
      ctx.fillStyle = '#EBF5FB'; // Light blue for database listings
    } else {
      ctx.fillStyle = '#FFFFFF'; // White default for manual items
    }
    ctx.fillRect(x, y, width, height);
    
    // Border - different colors for status
    let borderColor = '#BDC3C7';
    if (isSelected) {
      borderColor = '#2980B9';
    } else if (item.status === 'claimed') {
      borderColor = '#85929E';
    } else if (item.status === 'completed') {
      borderColor = '#27AE60';
    } else if (isExpiringSoon) {
      borderColor = '#E74C3C';
    } else if (isListing) {
      borderColor = '#5DADE2';
    }
    
    ctx.strokeStyle = borderColor;
    ctx.lineWidth = isSelected ? 2 : 1;
    ctx.strokeRect(x, y, width, height);
    
    // Item content
    const categoryEmojis = {
      dairy: 'ü•õ', meat: 'ü•©', vegetables: 'ü•¨', fruits: 'üçé',
      leftovers: 'üçï', beverages: 'ü•§', condiments: 'üçØ', other: 'üì¶'
    };
    
    ctx.font = '16px Arial';
    ctx.fillStyle = isSelected ? '#FFFFFF' : '#2C3E50';
    ctx.fillText(categoryEmojis[item.category] || 'üì¶', x + 5, y + 20);
    
    // Item name and quantity
    ctx.font = 'bold 12px Arial';
    ctx.textAlign = 'left';
    ctx.fillText(`${item.name} (${item.quantity})`, x + 30, y + 15);
    
    // Status and metadata line
    ctx.fillStyle = isSelected ? '#ECF0F1' : '#7F8C8D';
    ctx.font = '9px Arial';
    
    if (isListing) {
      const statusText = item.status ? `[${item.status?.toUpperCase()}]` : '[AVAILABLE]';
      const ownerText = item.ownerName ? `by ${item.ownerName}` : `by ${item.addedBy}`;
      ctx.fillText(`${statusText} ${ownerText}`, x + 30, y + 28);
      
      // Listing indicator
      ctx.fillStyle = isSelected ? '#FFFFFF' : '#3498DB';
      ctx.font = 'bold 8px Arial';
      ctx.fillText('üìã LISTING', x + width - 60, y + 15);
    } else {
      const addedText = `Added by ${item.addedBy} on ${item.dateAdded.toLocaleDateString()}`;
      ctx.fillText(addedText, x + 30, y + 28);
    }
    
    // Expiration warning
    if (isExpiringSoon && item.expirationDate) {
      ctx.fillStyle = isSelected ? '#FFFFFF' : '#E74C3C';
      ctx.font = 'bold 9px Arial';
      const daysLeft = Math.ceil((item.expirationDate.getTime() - Date.now()) / (24 * 60 * 60 * 1000));
      ctx.fillText(`‚ö†Ô∏è Expires in ${daysLeft} days!`, x + width - 120, y + 28);
    }
  }

  // Navigation methods
  public async selectNextItem(): Promise<void> {
    if (!this.uiState.currentFridge) return;
    const items = await this.getFridgeItems(this.uiState.currentFridge);
    if (items.length > 0) {
      this.uiState.selectedItemIndex = (this.uiState.selectedItemIndex + 1) % items.length;
    }
  }

  public async selectPreviousItem(): Promise<void> {
    if (!this.uiState.currentFridge) return;
    const items = await this.getFridgeItems(this.uiState.currentFridge);
    if (items.length > 0) {
      this.uiState.selectedItemIndex = this.uiState.selectedItemIndex > 0 
        ? this.uiState.selectedItemIndex - 1 
        : items.length - 1;
    }
  }

  public async deleteSelectedItem(playerId: string): Promise<boolean> {
    if (!this.uiState.currentFridge) return false;
    const items = await this.getFridgeItems(this.uiState.currentFridge);
    if (items.length > 0 && this.uiState.selectedItemIndex < items.length) {
      const item = items[this.uiState.selectedItemIndex];
      
      // Only allow deletion of manually added items (non-listings)
      if (item.listingId) {
        console.log('Cannot delete database listings - use claim/complete instead');
        return false;
      }
      
      const success = this.removeItem(this.uiState.currentFridge, item.id, playerId);
      if (success && this.uiState.selectedItemIndex >= items.length - 1) {
        this.uiState.selectedItemIndex = Math.max(0, items.length - 2);
      }
      return success;
    }
    return false;
  }

  public addRandomItem(fridgeId: string, playerId: string): boolean {
    const sampleItems = [
      { name: 'Leftover Sandwich', category: 'leftovers' as const, quantity: 1 },
      { name: 'Energy Drink', category: 'beverages' as const, quantity: 1 },
      { name: 'Yogurt', category: 'dairy' as const, quantity: 2 },
      { name: 'Apple', category: 'fruits' as const, quantity: 3 },
      { name: 'Carrot Sticks', category: 'vegetables' as const, quantity: 1 },
      { name: 'Cheese Slices', category: 'dairy' as const, quantity: 8 },
      { name: 'Orange Juice', category: 'beverages' as const, quantity: 1 },
      { name: 'Leftover Rice', category: 'leftovers' as const, quantity: 1 },
      { name: 'Bananas', category: 'fruits' as const, quantity: 4 },
      { name: 'Hot Sauce', category: 'condiments' as const, quantity: 1 }
    ];
    
    const randomItem = sampleItems[Math.floor(Math.random() * sampleItems.length)];
    
    return this.addItem(fridgeId, {
      ...randomItem,
      addedBy: playerId,
      expirationDate: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000)
    }, playerId);
  }

  // Database interaction methods
  public async claimSelectedItem(playerId: string): Promise<boolean> {
    if (!this.uiState.currentFridge) return false;
    
    const items = await this.getFridgeItems(this.uiState.currentFridge);
    if (items.length > 0 && this.uiState.selectedItemIndex < items.length) {
      const item = items[this.uiState.selectedItemIndex];
      
      if (item.listingId && item.status === 'available') {
        try {
          await this.apiService.claimListing(item.listingId);
          console.log(`‚úÖ Claimed listing: ${item.name}`);
          
          // Force refresh to get updated status
          this.lastRefresh.delete(this.uiState.currentFridge);
          await this.refreshFridgeData(this.uiState.currentFridge);
          
          return true;
        } catch (error) {
          console.error('Failed to claim listing:', error);
          return false;
        }
      } else {
        console.log('Item cannot be claimed - either not a listing or already claimed');
        return false;
      }
    }
    return false;
  }

  public async completeSelectedItem(playerId: string): Promise<boolean> {
    if (!this.uiState.currentFridge) return false;
    
    const items = await this.getFridgeItems(this.uiState.currentFridge);
    if (items.length > 0 && this.uiState.selectedItemIndex < items.length) {
      const item = items[this.uiState.selectedItemIndex];
      
      if (item.listingId && item.status === 'claimed') {
        try {
          await this.apiService.completeListing(item.listingId);
          console.log(`üèÜ Completed listing: ${item.name} - Points awarded!`);
          
          // Force refresh to get updated status
          this.lastRefresh.delete(this.uiState.currentFridge);
          await this.refreshFridgeData(this.uiState.currentFridge);
          
          return true;
        } catch (error) {
          console.error('Failed to complete listing:', error);
          return false;
        }
      } else {
        console.log('Item cannot be completed - either not a listing or not claimed');
        return false;
      }
    }
    return false;
  }

  public async addSampleListing(fridgeId: string, playerId: string): Promise<boolean> {
    if (!this.canPlayerModify(fridgeId, playerId)) {
      console.log(`Player ${playerId} cannot modify fridge ${fridgeId} - not a resident`);
      return false;
    }

    try {
      const fridge = this.fridgeData.get(fridgeId);
      const apartmentNumber = fridge?.apartmentId || undefined;
      
      const result = await this.apiService.createSampleListing(apartmentNumber, fridgeId);
      console.log(`üì∏ Created sample listing:`, result);
      
      // Force refresh to show new listing
      this.lastRefresh.delete(fridgeId);
      await this.refreshFridgeData(fridgeId);
      
      return true;
    } catch (error) {
      console.error('Failed to create sample listing:', error);
      return false;
    }
  }
}

export default FridgeManager;
